.headCase,
.headCaseMoreThree {
  margin: 0 auto;
  font-size: 26px;
  letter-spacing: 1px;
  font-weight: 100;
  margin-bottom: 15px;
}
.headCaseMoreThree {
  align-self: center;
  font-size: 36px;
}
.vizualContainer {
  padding: 20px 30px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: max-content;
}
.vizualContainerMoreThree {
  padding: 20px 30px;
  border-radius: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 5vw;
  grid-row-gap: 2vh;
  width: max-content;
}
.resultVariant {
  display: flex;
  gap: 20px;
  margin-top: 15px;
  padding: 3px 7px;
  border-radius: 10px;
}

