.container {
  padding: 20px;
  background-color: black;
  border-radius: 10px;
}
.loginOrSingUp {
  display: flex;
  gap: 20px;
}
.loginOrSingUp span {
  cursor: pointer;
  padding: 1px 15px;
  border-radius: 5px;
  font-size: 30px;
}
.login {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  gap: 20px;
}
.login input {
  width: 85%;
  font-size: 20px;
}
.login button {
  background-color: aqua;
  color: black;
  border: none;
  padding: 3px;
  border-radius: 2px;
  cursor: pointer;
  font-size: 24px;
}
.login span {
  cursor: pointer;
  font-size: 20px;
}
.mainContainer,
.colorsSection {
  background-color: rgba(0, 0, 0, 0.85);
  padding: 10px;
  border-radius: 20px;
  font-weight: 100;
}
.mainContainer input {
  color: black;
}
.questionsBlocks {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  /* gap: 10px; */
  margin-top: 10px;
  width: min-content;
}
.questionInBlock {
  padding: 7px;
  border: 2px solid green;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 5px;
  background-color: aquamarine;
}
.mainBlock {
  display: flex;
  flex-direction: column;
  gap: 10px;
  border: 3px solid black;
  padding: 10px;
  border-radius: 10px;
  background-color: bisque;
}
.blockVariants {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 5px;
}

.mainBlock input {
  width: 305px;
  height: 40px;
  border: 1px solid black;
  border-radius: 10px;
  padding: 5px 10px;
}
.blockVariants input {
  width: 150px;
  height: 30px;
  border: 1px solid black;
  border-radius: 10px;
  padding: 5px 10px;
}
.addBlock {
  padding: 5px 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 20px;
  cursor: pointer;
  background-color: aqua;
}
.navigateButton {
  color: black;
  padding: 3px 15px;
  font-size: 18px;
  border: none;
  border-radius: 5px;
  background-color: aqua;
  cursor: pointer;
}

.chooseColors {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
.chooseColors input {
  border: 1px solid white;
  margin-top: 5px;
}
.chooseColors input {
  cursor: pointer;
}
.casesContainer {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
}
.adminPanelSection {
  background-color: rgba(0, 0, 0, 0.85);
  padding: 5vh 5vw;
  border-radius: 1vw;
}
.currentCase {
  padding: 3px 15px;
  background-color: rgb(0, 46, 252);
  border-radius: 5px;
  cursor: pointer;
}
.deleteCase,
.caseStatus,
.activeSession {
  padding: 1px 6px;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
  background-color: rgb(0, 46, 252);
}
.caseStatus {
  background-color: transparent;
  cursor: auto;
}
.activeSession {
  background-color: transparent;
  border: 1px solid white;
  border-radius: 5px;
}
.pagination {
  width: 10px;
  height: 10px;
  border: 1px solid white;
  border-radius: 50%;
}
.deleteModalCss {
  position: absolute;
  top: 20vh;
  left: 0;
  right: 0;
  margin: 0 auto;
  padding: 3vh 5vw;
  width: max-content;
  border-radius: 1vw;
  background-color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.currentCaseElement {
  display: flex;
  gap: 10px;
}
.deleteModalCss p {
  font-size: 18px;
}
.deleteModalCss span {
  margin: 0 15px;
  font-size: 20px;
  padding: 5px 15px;
  cursor: pointer;
}
.deleteModalCss span:nth-child(1) {
  background-color: green;
}
.deleteModalCss span:nth-child(2) {
  background-color: red;
}
.sessionComponentContainer {
  background-color: rgba(0, 0, 0, 0.85);
  padding: 5vh 3vw;
  border-radius: 1vw;
  display: flex;
  gap: 2vw;
  position: relative;
}
.statusAndId {
  display: flex;
  align-items: center;
  flex-direction: column;
  align-items: start;
  gap: 1vh;
}
.currentStatus {
  padding: 1px 6px;
  border-radius: 5px;
  font-size: 18px;
}
.blockQuestionsContainer {
  margin-top: 10px;
  padding: 5px 10px;
  border-radius: 5px;
  background-color: rgb(78, 78, 78);
}
.links {
  margin-top: 10px;
  display: flex;
  gap: 10px;
}
.descriptionRextArea {
  margin-top: 20px;
  width: 100%;
  background-color: transparent;
  padding: 5px 10px;
  border-radius: 10px;
  font-size: 18px;
}
.descriptionRextArea::placeholder {
  font-size: 18px;
}
.foneImg {
  width: 30vw;
}
.comeback {
  cursor: pointer;
  width: 90px;
  font-size: 20px;
}

@media (max-width: 1000px) {
  .sessionComponentContainer {
    flex-direction: column;
  }
  .foneImg {
    width: 60vw;
  }
  .comeback {
    width: 100%;
    font-size: 25px;
    margin-bottom: 30px;
  }
  .questionsBlocks{
    display: block;
  }
}
@media (max-width: 750px) {
  .currentCaseElement{
    flex-direction: column;
    border: 1px solid rgb(0, 238, 255);
    padding: 10px 15px;
    border-radius: 10px;
  }
.currentCase{
  order: 1;
  padding:20px 15px ;
}
.caseStatus{
  order: -1;
  width: max-content;
  margin-left: auto;
}
.activeSession{
  order: 1;
  text-align: center;
}
.deleteCase{
  width: max-content;
  margin-left: auto;
  order: -1;
  background-color: transparent;
  padding: 0;
  font-size: 22px;
}
}
@media (max-width: 500px) {
  .foneImg {
    width: 80vw;
  }
  
}

